import { configureStore, createSlice } from '@reduxjs/toolkit';
import data from '../data';

let board = createSlice({
	name: 'board',
	initialState: data,
	reducers: {
		createBoard(state, action) {
			state.push(action.payload);
		},
		updateBoard(state, action) {
			let idx = state.findIndex((i) => {
				return i.id == action.payload.id;
			});

			state[idx].name = action.payload.name;
			state[idx].country = action.payload.country;
			state[idx].city = action.payload.city;
			state[idx].salary = action.payload.salary;
		},
		deleteBoard(state, action) {
			let idx = state.findIndex((i) => {
				return i.id == action.payload.id;
			});
			state.splice(idx, 1);
			//state = state.filter((item) => item.id != action.payload.id);
		},
	},
});

export let { createBoard, updateBoard, deleteBoard } = board.actions;

export default configureStore({
	reducer: {
		board: board.reducer,
	},
});
