import { useState, useEffect } from 'react';
import { updateBoard, deleteBoard } from '../store/store';
import { useDispatch, useSelector } from 'react-redux';
import { useParams, useNavigate } from 'react-router-dom';
// reactstrap components
import { Button, Card, CardHeader, CardBody, CardFooter, CardTitle, FormGroup, Form, Input, Row, Col } from 'reactstrap';

function BoardDetail() {
	let board = useSelector((state) => state.board);
	let dispatch = useDispatch();
	let { id } = useParams();
	let data = board.find((x) => x.id == id);
	const navigate = useNavigate();

	const handleSubmit = (e) => {
		e.preventDefault();

		if (!e.target.name.value && true) {
			alert('name 입력해라');
		} else if (!e.target.country.value && true) {
			alert('country 입력해라');
		} else if (!e.target.city.value && true) {
			alert('city 입력해라');
		} else if (!e.target.salary.value && true) {
			alert('salary 입력해라');
		} else {
			if (window.confirm('글을 수정 할 텐가?')) {
				dispatch(
					updateBoard({
						id: id,
						name: e.target.name.value,
						country: e.target.country.value,
						city: e.target.city.value,
						salary: e.target.salary.value,
					})
				);

                navigate('/admin/board');
			}
		}
	};

	return (
		<div className="content">
			<Row>
				<Col>
					<Card className="card-user">
						<CardHeader>
							<CardTitle tag="h5">Edit Post</CardTitle>
						</CardHeader>
						<CardBody>
							<Form onSubmit={handleSubmit}>
								<Row>
									<Col className="pr-1" md="6">
										<FormGroup>
											<label>NAME</label>
											<Input type="text" name="name" placeholder="name" defaultValue={data.name} />
										</FormGroup>
									</Col>
									<Col className="pl-1" md="6">
										<FormGroup>
											<label>COUNTRY</label>
											<Input type="text" name="country" placeholder="country" defaultValue={data.country} />
										</FormGroup>
									</Col>
								</Row>
								<Row>
									<Col className="pr-1" md="6">
										<FormGroup>
											<label>CITY</label>
											<Input type="text" name="city" placeholder="city" defaultValue={data.city} />
										</FormGroup>
									</Col>
									<Col className="pl-1" md="6">
										<FormGroup>
											<label>SALARY</label>
											<Input type="text" name="salary" placeholder="salary" defaultValue={data.salary} />
										</FormGroup>
									</Col>
								</Row>
								<Row>
									<div className="update ml-auto mr-auto">
										<Button className="btn-round" color="primary" type="submit">
											수정하기
										</Button>
										<Button
											className="btn-round"
											color="danger"
											type="button"
											onClick={() => {
												if (window.confirm('삭제 할 텐가?')) {
													dispatch(deleteBoard({ id: id }));
													navigate('/admin/board');
												}
											}}
										>
											삭제하기
										</Button>
										<Button
											className="btn-round"
											color="warning"
											type="button"
											onClick={() => {
												navigate('/admin/board');
											}}
										>
											목록으로
										</Button>
									</div>
								</Row>
							</Form>
						</CardBody>
					</Card>
				</Col>
			</Row>
		</div>
	);
}

export default BoardDetail;
