import { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
// reactstrap components
import { Card, CardHeader, CardBody, CardFooter, CardTitle, Table, Row, Col, Button } from 'reactstrap';

function Tables() {
	let board = useSelector((state) => state.board);
	let navigate = useNavigate();

	return (
		<>
			<div className="content">
				<Row>
					<Col md="12">
						<Card>
							<CardHeader>
								<CardTitle tag="h4">Simple Table</CardTitle>
							</CardHeader>
							<CardBody>
								<Table responsive>
									<thead className="text-primary">
										<tr>
											<th>Name</th>
											<th>Country</th>
											<th>City</th>
											<th className="text-right">Salary</th>
										</tr>
									</thead>
									<tbody>
										{board.map((a, i) => (
											<tr
												key={i}
												onClick={() => {
													navigate(`/admin/boardDetail/${board[i].id}`);
												}}
											>
												<td>{board[i].name}</td>
												<td>{board[i].country}</td>
												<td>{board[i].city}</td>
												<td className="text-right">{board[i].salary}</td>
											</tr>
										))}
									</tbody>
								</Table>
							</CardBody>
							<CardFooter>
								<Button
									className="float-right"
									color="primary"
									outline
									onClick={() => {
										navigate('/admin/boardAdd');
									}}
								>
									등록
								</Button>
							</CardFooter>
						</Card>
					</Col>
				</Row>
			</div>
		</>
	);
}

export default Tables;
