import { useState, useEffect } from 'react';
import { createBoard } from '../store/store';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
// reactstrap components
import { Button, Card, CardHeader, CardBody, CardFooter, CardTitle, FormGroup, Form, Input, Row, Col } from 'reactstrap';

function BoardAdd() {
	let board = useSelector((state) => state.board);
	let dispatch = useDispatch();
	const navigate = useNavigate();

	const handleSubmit = (e) => {
		e.preventDefault();

		if (!e.target.name.value && true) {
			alert('name 입력해라');
		} else if (!e.target.country.value && true) {
			alert('country 입력해라');
		} else if (!e.target.city.value && true) {
			alert('city 입력해라');
		} else if (!e.target.salary.value && true) {
			alert('salary 입력해라');
		} else {
			if (window.confirm('글을 등록 할 텐가?')) {
				dispatch(
					createBoard({
						id: board.length + 1,
						name: e.target.name.value,
						country: e.target.country.value,
						city: e.target.city.value,
						salary: e.target.salary.value,
					})
				);
				navigate('/admin/board');
			}
		}
	};

	return (
		<div className="content">
			<Row>
				<Col>
					<Card className="card-user">
						<CardHeader>
							<CardTitle tag="h5">Add Post</CardTitle>
						</CardHeader>
						<CardBody>
							<Form onSubmit={handleSubmit}>
								<Row>
									<Col className="pr-1" md="6">
										<FormGroup>
											<label>NAME</label>
											<Input type="text" name="name" placeholder="name" />
										</FormGroup>
									</Col>
									<Col className="pl-1" md="6">
										<FormGroup>
											<label>COUNTRY</label>
											<Input type="text" name="country" placeholder="country" />
										</FormGroup>
									</Col>
								</Row>
								<Row>
									<Col className="pr-1" md="6">
										<FormGroup>
											<label>CITY</label>
											<Input type="text" name="city" placeholder="city" />
										</FormGroup>
									</Col>
									<Col className="pl-1" md="6">
										<FormGroup>
											<label>SALARY</label>
											<Input type="text" name="salary" placeholder="salary" />
										</FormGroup>
									</Col>
								</Row>
								<Row>
									<div className="update ml-auto mr-auto">
										<Button
											className="btn-round"
											color="warning"
											type="button"
											onClick={() => {
												navigate('/admin/board');
											}}
										>
											목록으로
										</Button>
										<Button className="btn-round" color="primary" type="submit">
											등록
										</Button>
									</div>
								</Row>
							</Form>
						</CardBody>
					</Card>
				</Col>
			</Row>
		</div>
	);
}

export default BoardAdd;
